.tags-card{
    height: 400px;
    background-color: rgb(60, 60, 60);
    border-radius: 10px;
    overflow: hidden;
    transition: .2s all;
}
.tags-card:hover{
    transform: scale(1.05);
    box-shadow: 0px 0px 20px 5px rgba(0, 0, 0, 0.5);
}
.tags-card-img{
    position: relative;
    height: 250px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.tags-card-hero{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 20px;
}
.tags-card-avatar{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.5);
}
.tags-card-avatar img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 1px solid #aaa;
    object-fit: cover;
}
.tags-card-title{
    display: block;
    margin-top: 20px;
    color: #fff;
    font-size: larger;
    z-index: 1;
}
.tags-card-des{
    padding: 20px;
    font-size: 16px;
    color: #aaa;
}


@media only screen and (max-width: 426px){
    .tags-card{
        height: 300px;
    }
    .tags-card-img{
        height: 200px;
    }
    .tags-card-hero{
        padding: 15px;
    }
    .tags-card-des{
        font-size: 12px;
        padding: 15px;
        padding-top: 5px;
    }
}
