.related{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.related-toon-cont{
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
}

.related-card{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.related-card-img{
    width: 100%;
    height: auto;
}
.related-card-img img{
    width: 100%;
}

.related-card-title{
    color: #fff;
    font-size: 16px;
    transition: 0.2s all;
}
.related-card-title:hover{
    color: var(--main-col);
}

.related-card-date{
    color: #aaa;
}

@media only screen and (max-width: 426px){
    .related-toon-cont{
        grid-gap: 10px;
        grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
    }
}