.browser-model-wrapper {
  margin-top: 65px;
  background-color: #333;
}
.browser-model-cont {
  max-width: 1200px;
  padding: var(--pad-m);
  margin: 0 auto;
}
.browser-model {
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0px -20px 40px 5px rgba(0, 0, 0, 0.5);
}
.browser-model::after {
  content: "";
  clear: both;
  display: table;
}

.browser-model section {
  float: left;
  width: 25%;
  transition: 0.3s all;
}

.browser-card {
  position: relative;
  height: 70vh;
  overflow: hidden;
  transition: 0.3s all;
}
.browser-card:hover > .browser-card-bg {
  transform: scale(1.1);
}
.browser-card:hover > .browser-card-info {
  color: var(--main-col);
}

.browser-card-bg {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: 0.3s all;
}
.browser-card-info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
  padding: 30px 20px;
  color: #fff;
  transition: 0.3s color;
}

.browser-card-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  transition: 0.3s all;
}
.browser-card-title {
  font-size: 1.5rem;
  margin-bottom: 20px;
  transition: 0.3s all;
}
.browser-card-des {
  font-size: 1.2rem;
  margin-bottom: 30px;
  transition: 0.3s all;
}

@media only screen and (max-width: 769px) {
  .browser-model section {
    width: 50%;
  }
  .browser-card {
    height: 300px;
  }
  .browser-card-bg {
    background-position: top;
  }
  .browser-card-info {
    padding: 20px 10px;
  }
  .browser-card-icon {
    font-size: 2.2rem;
    margin-bottom: 15px;
  }
  .browser-card-title {
    font-size: 1.2rem;
    margin-bottom: 15px;
  }
  .browser-card-des {
    font-size: 1rem;
    margin-bottom: 25px;
  }
}

@media only screen and (max-width: 426px) {
  .browser-model section {
    width: 50%;
  }
  .browser-card {
    height: 250px;
  }
  .browser-card-bg {
    background-position: top;
  }
  .browser-card-info {
    padding: 12px 8px;
  }
  .browser-card-icon {
    font-size: 2rem;
    margin-bottom: 8px;
  }
  .browser-card-title {
    font-size: 1rem;
    margin-bottom: 8px;
  }
  .browser-card-des {
    font-size: 0.8rem;
    margin-bottom: 12px;
  }
}
