nav {
  position: fixed;
  top: 0;
  height: 65px;
  width: 100%;
  color: #fff;
  z-index: 10;
}
.nav-main {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(5px);
  padding: 0 20px;
  height: 100%;
  display: flex;
  justify-content: space-between;
}
.nav-main section {
  height: 100%;
  display: flex;
  align-items: center;
  overflow: hidden;
  flex-basis: 33.33%;
}
.nav-sec--a {
  justify-content: flex-start;
}
.nav-sec--b {
  justify-content: center;
}
.nav-sec--c {
  justify-content: flex-end;
}

.nav-logo a {
  display: flex;
  align-items: center;
}
.nav-logo img {
  height: 40px;
}

.nav-search {
  position: relative;
  height: 40px;
  width: 80%;
}
.nav-search input {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  padding: 10px 15px;
  border: none;
  outline: none;
  border-radius: 5px;
  font-size: 16px;
  transition: 0.2s all;
}
.nav-search input:hover,
.nav-search input:focus {
  box-shadow: 0px 0px 50px 12px rgba(0, 0, 0, 0.5);
  transform: translateY(-2%);
}
.nav-search input::placeholder {
  color: #ccc;
}

.nav-signin,
.nav-btn {
  background: transparent;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  height: 40px;
  border: none;
  outline: none;
  border-radius: 5px;
  transition: 0.2s all;
}
.nav-signin:hover,
.nav-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(5px);
}
.nav-signin {
  padding: 0 25px;
  margin-right: 20px;
}
.nav-btn {
  height: 40px;
  width: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  user-select: none;
  font-size: 1.75rem;
  border: none;
  outline: none;
  background: transparent;
  color: #ffffff;
  cursor: pointer;
}
.nav-btn span:nth-child(3) {
  margin-bottom: 0;
}

.nav-sidebar {
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(5px);
  position: absolute;
  right: 0;
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
  width: 350px;
  height: calc(100vh - 65px);
  transform: translateX(100%);
  transition: 0.3s ease-in-out;
  opacity: 0;
  overflow-y: auto;
  z-index: 10;
}
.nav-sidebar.active {
  opacity: 1;
  transform: translateX(0%);
}
.sidebar-cont {
  list-style: none;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}
.sidebar-cont:first-child {
  border-top: none;
}
.sidebar-cont h2 {
  color: #aaa;
  font-size: 16px;
  font-weight: lighter;
  text-transform: uppercase;
  padding: 20px;
}
.sidebar-links {
  background: transparent;
  color: #fff;
  display: block;
  width: 100%;
  padding: 20px;
  font-size: 16px;
  transition: 0.2s all;
}
.sidebar-links:hover {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(5px);
}

.sidebar-links .fa-icon {
  margin-right: 30px;
}

@media only screen and (max-width: 426px) {
  .nav-signin {
    display: none;
  }
  .nav-sidebar {
    width: 100%;
  }
}

@media only screen and (max-width: 768px) {
  .nav-sec--b {
    display: none !important;
  }
  .nav-main section {
    flex-basis: auto;
  }
}
