.search-page{
    width: 100%;
    padding: 0px var(--pad-m);
    padding-top: 65px;
    margin: 65px auto;
}

.pagination{
    margin-top: 30px;
    display: flex;
    justify-content: center;
    gap: 15px;
    flex-wrap: wrap;
}

.pagination-btn{
    flex-shrink: 0;
    display: block;
    width: 40px;
    height: 40px;
}

.pagination-btn button{
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
    transition: .2s;
}
.pagination-btn button:hover{
    background: rgba(255, 255, 255, 0.5);
}