footer {
  border-top: 1px inset rgba(255, 255, 255, 0.4);
  display: flex;
  flex-direction: column;
}

.footer-main {
  background: #222;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 50px 20px;
}

.footer-socials-cont {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
}
.footer-socials-cont span {
  background: #ff9a19;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: 0.3s all;
}
.footer-socials-cont span:hover {
  background: #fff;
}
.footer-socials-cont span:hover > .footer-socials {
  color: #ff9a19;
}
.footer-socials {
  width: 25px;
  height: 25px;
  color: #fff;
  transition: 0.3s all;
}

.footer-nav {
  margin-top: 30px;
}
.footer-nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.footer-nav ul li {
  list-style: none;
  display: block;
  border-right: 1px solid #aaa;
}
.footer-nav ul li:last-child {
  border: none;
}
.footer-nav-links {
  margin: 0px 15px;
  font-size: 16px;
  color: #aaa;
  transition: 0.2s;
}
.footer-nav-links:hover {
  color: #fff;
}

.footer-disc {
  margin-top: 30px;
  font-size: 16px;
  text-align: center;
  color: #aaa;
}

.footer-secondary {
  background: #555;
  padding: 20px;
}
.footer-secondary p {
  text-align: center;
  color: #777;
}

@media only screen and (max-width: 426px) {
  .footer-socials-cont {
    gap: 15px;
  }
  .footer-socials-cont span {
    height: 40px;
    width: 40px;
  }
  .footer-socials {
    height: 25px;
    width: 25px;
  }
  .footer-nav-links {
    font-size: 16px;
  }
  .footer-disc {
    font-size: 14px;
  }
}
