@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,600;0,700;0,800;1,400;1,600;1,800&display=swap");

:root {
  --main-col: #ff9a19;
  --bg-col: #222222;
  --pad-m: 30px;
  --pad-s: 15px;

  /* Screen width */
  /* --screen-mobile-s: 426px;
    --screen-mobile-m: 426px;
    --screen-mobile-l: 426px;
    --screen-tablet-s: 601px;
    --screen-tablet-l: 769px;
    --screen-laptop: 1025px; */
}

p {
  color: #bbbbbb;
}

.page-full {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  padding: 0px var(--pad-m);
  margin: 65px auto;
}
.page-grid {
  width: 100%;
  max-width: 1200px;
  margin: 65px auto;
  padding: 0px var(--pad-m);
  display: grid;
  grid-template-columns: 3fr 1.2fr;
  grid-gap: 30px;
}
.page-margin-top {
  margin-top: 65px;
}

.posts-grid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 30px;
  margin-top: 50px;
}

.posts-head {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid #aaa;
}
.posts-head-icon {
  background: var(--main-col);
  color: #fff;
  padding: 10px;
  font-size: 20px;
  border: 1px solid #aaa;
  border-bottom: none;
}
.posts-heading {
  margin-left: 10px;
  color: #aaa;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 1.5px;
}

.posts-footer {
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
.line-break {
  background-color: #aaa;
  height: 2px;
  width: 100%;
}
.load-more {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  max-width: 200px;
  width: 100%;
  height: 45px;
  background: none;
  color: var(--main-col);
  border: 1px solid var(--main-col);
  border-radius: 30px;
  border-bottom: 5px solid;
  cursor: pointer;
  transition: 0.3s all;
}
.load-more:hover {
  background: var(--main-col);
  color: #fff;
  border: 1px solid #fff;
}

.chaps {
  margin-top: 30px;
}

@media only screen and (max-width: 769px) {
  .page-grid {
    grid-template-columns: 1fr;
  }
  .posts-grid {
    grid-gap: 15px;
  }
}

@media screen and (max-width: 475px) {
  .page-grid,
  .page-full {
    padding: 0px var(--pad-s);
  }
  .posts-grid {
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
    grid-gap: 10px;
  }
  .posts-grid,
  .posts-footer {
    margin-top: 30px;
  }
  .line-break {
    display: none;
  }
}
