.chapter-cont{
    margin: 65px auto;
    padding: 0px var(--pad-m);
    padding-top: 65px;
    width: 100%;
    max-width: 1000px;
}

.chapter-head-title{
    font-size: 24px;
    color: #fff;
    font-weight: 600;
}
.chapter-head-btns{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 30px;
}
.chapter-head-btns section{
    flex-shrink: 0;
}
.chapter-head button, .chapter-head select{
    background: rgba(255, 255, 255, 0.2);
    color: var(--main-col);
}
.chapter-head button:hover{
    background: var(--main-col);
    color: #fff;
}
.chapter-head-action-btns{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: none;
    outline: none;
    margin-right: 15px;
    cursor: pointer;
    font-size: 1.5rem;
    transition: 0.2s;
}
.chapter-head-selector{
    max-width: 200px;
    width: 100%;
    flex-shrink: 1;
}
.chapter-head-selector select{
    height: 40px;
    padding: 0 12px;
    border: none;
    outline: none;
    border-radius: 5px;
    font-size: 1rem;
    color: #aaa;
    width: 100%;
}
.chapter-head-nav-btns{
    height: 40px;
    padding: 0 15px;
    border: none;
    outline: none;
    border-radius: 5px;
    margin-right: 5px;
    font-size: 1.2rem;
    transition: 0.2s;
    cursor: pointer;
}

.chapter{
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.chapter img{
    width: 100%;
    max-width: 700px;
}

@media only screen and (max-width: 426px){
    .chapter-cont{
        padding: 0px var(--pad-s);
        padding-top: 65px;
    }
    
    .chapter-head-title{
        font-size: 18px;
    }
    .chapter-head-action-btns{
        width: 30px;
        height: 30px;
        margin-right: 10px;
        font-size: 18px;
    }
    .chapter-head-selector select{
        height: 30px;
    }
    .chapter-head-nav-btns{
        height: 30px;
    }
    .chapter{
        margin-top: 30px;
        padding: -15px;
    }
    .chapter-head-selector{
        display: none;
    }
}