.home{
    color: #fff;
}
.home--landing{
    position: relative;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.landing-info{
    max-width: 900px;
    padding: 30px;
}
.landing-info h1{
    color: #fff;
    font-size: 30px;
    margin-bottom: 20px;
}
.landing-info p{
    color: #bbb;
    font-size: 18px;
}
.landing-scorll{
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translateX(-50%);
}

@media only screen and (max-width: 426px){
    .landing-info{
        padding: 15px;
    }
}