
.widget-support{
    padding: 10px;
}
.widget-support legend{
    color: #aaa;
    padding: 10px;
}
.widget-support-icons-cont{
    display: flex;
    justify-content: space-evenly;
    gap: 10px;
    flex-wrap: wrap;
    color:  #fff;
    padding: 10px 0 20px;
}
.widget-support-icons{
    font-size: 30px;
}

.widget-search{
    margin-top: 30px;
}
.widget-search input{
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    padding: 15px;
    border: none;
    outline: none;
    border-radius: 5px;
    font-size: 16px;
    transition: 0.2s all;
}
.widget-search input:hover, .widget-search input:focus{
    transform: translateY(-2%);
}
.widget-search input::placeholder{
    color: #aaa;
}

.widget-section{
    margin-top: 30px;
}
.widget-section-head{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 10px;
    border-bottom: 1px solid #aaa;
}
.widget-section-icon{
    background: var(--main-col);
    color: #fff;
    padding: 10px;
    font-size: 20px;
    border: 1px solid #aaa;
    border-bottom: none;
}
.widget-section-heading{
    margin-left: 10px;
    font-size: 18px;
    font-weight: 600;
    color: #aaa;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}
.widget-section-cards{
    padding: 10px 0px;
}
.widget-section-cards li{
    list-style: none;
}

/* Widget Cards */

.widget-card{
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.wc-head{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.wc-pos{
    height: 145px;
    width: 105px;
    object-fit: cover;
}

.wc-info{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.wc-title{
    color: #fff;
    font-size: 16px;
    font-weight: 600;
}

.wc-ltst button{
    background-color: transparent;
    display: block;
    border: none;
    border: 1px solid #FF9A19;
    border-radius: 5px;
    color: #FF9A19;
    padding: 8px 12px;
    margin-bottom: 5px;
    outline: none;
    cursor: pointer;
    transition: .2s all;
}
.wc-ltst button:hover{
    background-color: #ff9a19;
    border-color: #fff;
    color: #fff;
}


@media only screen and (max-width: 256px){
    .widget-card{
        flex-direction: column;
    }
}