.discussion{
    margin-top: 30px;
}

.feedback-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 30px;
}
.feedback-ques,
.comment-text,
.comments-section-heading{
    color: #fff;
    font-size: 18px;
    font-weight: 600;
}
.feedback-res{
    display: block;
    margin-top: 10px;
    color: #aaa;
    font-size: 18px;
    font-weight: lighter;
}
.feedback-emotes-cont{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}
.emote-btn{
    background: transparent;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 10px;
    cursor: pointer;
}
.emote-btn:hover > .emote{
    transform: scale(1.2);
}
.emote{
    font-size: 40px;
    transition: .3s all;
}
.emote-text{
    color: #aaa;
}

.comment-box-cont{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.comment-box{
    display: flex;
    gap: 20px;
    flex-direction: column;
}
.comment-box textarea[name='comment']{
    background: rgba(255, 255, 255, .2);
    width: 100%;
    border: none;
    outline: none;
    border-radius: 5px;
    color: #fff;
    height: 150px;
    padding: 20px;
    font-size: 16px;
    resize: vertical;
}
.comment-box textarea[name='comment']::placeholder{
    color: #aaa;
}
.comment-box textarea[name='comment']:hover, .comment-box textarea[name='comment']:focus{
    border: 1px solid #fff;
}
.comment-post-btn{
    text-align: right;
}
.comment-post-btn button{
    background-color: var(--main-col);
    color: #fff;
    font-size: 16px;
    padding: 10px 20px;
    border: none;
    border: 1px solid var(--main-col);
    border-radius: 25px;
    outline: none;
    cursor: pointer;
    transition: .3s;
}
.comment-post-btn button:hover{
    color: var(--main-col);
    background-color: #fff;
}

.comments-section{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.comments-cont{
    display: flex;
    flex-direction: column;
}

.comment{
    display: flex;
    gap: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, .1);
    margin-bottom: 20px;
}
.comment:last-child{
    border: none;
}
.comment-img{
    flex-shrink: 0;
    width: 75px;
    height: 75px;
}
.comment-img img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
}
.comment-info{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.comment-commenter{
    color: #fff;
    font-size: 16px;
    font-weight: 600;
}
.comment-para{
    color: #aaa;
    font-size: 16px;
}
.comment-date{
    color: #aaa;
    font-size: 12px;
}



@media only screen and (max-width: 426px){
    .feedback-emotes-cont{
        max-width: 250px;
        width: 100%;
    }
}