.log-form-cont{
    min-height: 100vh;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 95px 20px;
}

.log-form{
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(5px);
    box-shadow: 0px 0px 40px 15px rgba(0, 0, 0, 0.5);
    min-height: 500px;
    max-width: 450px;
    width: 100%;
    border-radius: 5px;
}

.log-form-head{
    padding: 30px;
    text-align: center;
}

.log-form-logo{
    height: auto;
    width: 50%;
    margin-bottom: 10px;
}
.log-form-title{
    color: #fff;
    font-size: 1.5rem;
    font-weight: 600;
    text-transform: uppercase;
}

.log-form-body{
    padding: 0px 30px;
}
.log-form-inp{
    position: relative;
    height: 50px;
    margin-bottom: 30px;
    overflow: hidden;
}
.log-form-inp input{
    background: transparent;
    width: 100%;
    height: 100%;
    padding-left: 10px;
    padding-top: 20px;
    font-size: 16px;
    border: none;
    outline: none;
    color: #fff;
    letter-spacing: 1.2px;
}
.log-form-inp label{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-bottom: 2px solid rgba(0, 0, 0, 0.3);
    pointer-events: none;
}
.log-form-inp label::before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    border-bottom: 2px solid var(--main-col);
    width: 100%;
    height: 100%;
    transform: translateX(-101%);
    transition: 0.3s;
}
.log-form-inp label span{
    position: absolute;
    left: 10px;
    bottom: 2px;
    font-size: 18px;
    color: #aaa;
    letter-spacing: 1.2px;
    transition: 0.3s;
}

.log-form-inp input:focus + label span,
.log-form-inp input:valid + label span{
    transform: translateY(-130%);
    font-size: 14px;
    color: var(--main-col);
}

.log-form-inp input:focus + label::before,
.log-form-inp input:valid + label::before{
    transform: translateY(0%);
}

.log-form-submit{
    padding: 15px;
    width: 100%;
    background: var(--main-col);
    color: #fff;
    font-size: 18px;
    letter-spacing: 1.5px;
    margin-bottom: 30px;
    border: none;
    outline: none;
    border: 1px solid var(--main-col);
    border-radius: 3px;
    cursor: pointer;
    transition: 0.2s;
}
.log-form-submit:hover{
    background: #222;
    color: var(--main-col);
}

.log-form-footer{
    background: #222;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.log-form-footer-text{
    color: #aaa;
    font-size: 1rem;
    margin-bottom: 30px;
}

.log-form-redirect{
    padding: 15px;
    width: 100%;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 3px;
    color: #fff;
    font-size: 18px;
    text-align: center;
    letter-spacing: 1.5px;
    border: none;
    outline: none;
    transition: 0.2s;
}
.log-form-redirect:hover{
    background: rgba(255, 255, 255, 0.5);
}
