.card {
  position: relative;
  width: 100%;
  overflow: hidden;
  border-radius: 10px;
}
.card::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
  display: block;
  width: 100%;
  height: 100%;
}

.card-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: #fff;
  z-index: 1;
}

.card-bg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.3s all;
}
.card:hover .card-bg {
  transform: scale(1.1);
}

.card-icons {
  background: linear-gradient(
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 0.9),
    rgba(0, 0, 0, 0)
  );
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}
.card-btn {
  font-size: 24px;
  cursor: pointer;
}
.card-last-chap {
  position: relative;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  border: none;
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  padding: 5px 8px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s;
}
.card-last-chap:hover {
  background: rgba(255, 255, 255, 0.5);
}
.card-title {
  font-size: 16px;
  font-weight: 600;
}
.card-title::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 25%;
  width: 100%;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 0.9),
    rgba(0, 0, 0, 0)
  );
  z-index: -1;
}

@media only screen and (max-width: 426px) {
  .card-box,
  .card-icons {
    padding: 8px;
  }
  .card-btn {
    font-size: 18px;
  }
  .card-last-chap {
    font-size: 12px;
  }
}
