.Toon-header{
    margin-top: 65px;
    position: relative;
    padding: 30px;
}

.Toon-header-banner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.Toon-header-cont{
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(5px);
    padding: 30px 60px;
    width: 100%;
    max-width: 1200px;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 8px;
    margin: 0 auto;
    box-shadow: 10px 10px 20px 10px rgba(0, 0, 0, 0.5);
}

.Toon-header-title{
    color: var(--main-col);
    font-size: 22px;
    letter-spacing: 1.5px;
    margin-bottom: 20px;
}

.Toon-header-info-cont{
    display: flex;
    gap: 60px;
    overflow: hidden;
}

.Toon-header-img{
    flex-shrink: 0;
    width: 300px;
    height: 450px;
    border-radius: 5px;
    border: 2px solid #fff;
    overflow: hidden;
}

.Toon-header-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.Toon-header-info{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.Toon-header-rating{
    display: flex;
    align-items: center;
    font-size: 28px;
    color: #fff;
    margin-bottom: 20px;
}
.Toon-header-info-flex div{
    display: flex;
    margin-bottom: 10px;
}
.Toon-header-info-flex div h4{
    width: 110px;
    flex-shrink: 0;
    color: #fff;
}
.Toon-header-info-flex div p{
    color: #aaa;
}

.Toon-header-btns-cont{
    display: flex;
}
.Toon-header-btns-cont section{
    width: 110px;
    text-align: center;
}
.Toon-header-btns-cont section span{
    color: #aaa;
    display: block;
    margin-top: 15px;
}
.Toon-header-btns-read, .Toon-header-btns-bookmark{
    background-color: transparent;
    color: var(--main-col);
    border: none;
    outline: none;
    padding: 0;
    margin: 0 auto;
    border-radius: 5px;
    cursor: pointer;
    font-size: 2rem;
    display: block;
    flex-grow: 0;
}

.des p{
    position: relative;
    color: #aaa;
    line-height: 2;
    max-height: 200px;
    margin-top: 20px;
    padding-bottom: 50px;
    overflow: hidden;
}
.des p.active{
    max-height: 100%;
}
.des p::after, .chaps-cont::after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50px;
    background: linear-gradient(rgba(0,0,0,0), var(--bg-col));
}
.show-more-dets{
    text-align: center;
}
.show-more-dets button{
    background: transparent;
    color: var(--main-col);
    border: none;
    outline: none;
    font-size: 18px;
    cursor: pointer;
}

.chaps-cont{
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    max-height: 390px;
    padding-bottom: 50px;
    overflow: hidden;
    transition: 0.5s all;
}
.chaps-cont.active{
    max-height: 100%;
}

.chaps-links{
    display: flex;
    justify-content: space-between;
    gap: 10px;
    flex-wrap: wrap;
    padding: 20px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.chaps-num{
    color: #fff;
}
.chaps-date{
    color: #aaa;
}



@media only screen and (max-width: 769px){
    .Toon-header-cont{
        padding: 20px 40px;
    }
    .Toon-header-info-cont{
        flex-direction: column;
        align-items: center;
        gap: 40px;
    }
    .Toon-header-img{
        width: 250px;
        height: 375px;
    }
}

@media only screen and (max-width: 426px){
    .Toon-header{
        padding: 15px;
    }
    .Toon-header-cont{
        padding: 12px 18px;
    }
    .Toon-header-info-cont{
        flex-direction: column;
        align-items: center;
        gap: 40px;
    }
    .Toon-header-img{
        width: 150px;
        height: 225px;
    }
    .Toon-header-btns-cont{
        justify-content: space-between;
        gap: 10px;
    }
    .Toon-header-btns-cont section{
        width: 100%;
    }
}

@media only screen and (max-width: 320px){
    .Toon-header-info-flex div{
        flex-direction: column;
    }
}
